body {
  transition: 0.3s;

  &:after {
    content: '';
    background-color: @darkBlue;
    opacity: 0;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 1;
    transition: 0.3s;
    pointer-events: none;
  }

  &.mainpage {
    position: relative;
  }
  &.no-scroll {
    overflow: hidden;
  }
  &.opacity-layer {
    position: relative;
    transition: 0.3s;


    &:after {      
      opacity: 0.6;
      pointer-events: all;
    }    
  }
}

