  right: -15px;
  width: 15px;
  height: 15px;
  background-image: url('../img/icons/menu-arrow.png');
  background-size: 7px 5px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  transform: rotate(0);
  transition: 0.3s;
}
.header__menu-drop.active {
  transform: rotate(0);
}
.header__menu-sub {
  display: none;
