  bottom: 139px;
  left: calc(50% + 10px);
  width: auto;
}
.slider .slick-dots li button:before {
  content: '';
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 8px;
  height: 8px;
  background-color: #ffffff;
